
import i18n from "@/i18n";
import { ELEMENT_COLOR_CLASS, TElementColorClassKey } from "@/master";
import { computed, defineComponent, reactive, ref, watch } from "vue";
import CompositionFunction from "./CompositionFunction.vue";

export default defineComponent({
  name: "DamageResult",
  props: {
    damageResult: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const { displayName } = CompositionFunction();

    const  = reactive(props.damageResult.);
    const  = ref("");
    const elementClass = (item: string) =>
      ELEMENT_COLOR_CLASS[item as TElementColorClassKey];
    const resultStyleRef = ref("1");

    watch(, () => {
      if (
        (.value == "" && . == 0) ||
        (.value == "" && . == 0) ||
        (.value == "" && . == 0) ||
        (.value == "" && . == 0)
      ) {
        .value = "";
        (document.getElementById("-") as HTMLInputElement).checked = true;
      }
    });

    const swirlDmg = computed(() => {
      let result = .;
      if (.value == "" && ["", ""].includes(.)) {
        result *= .;
      } else if (.value == "" && ["", ""].includes(.)) {
        result *= .;
      } else if (.value == "" && . == "") {
        result += .;
      }
      return result;
    });

    const reactionDmg = (reaction: string): number => {
      let result = [reaction];
      if (result) {
        const critRate = [reaction + ''];
        if (critRate) {
          const critDmg = [reaction + ''];
          if (critDmg) {
            result *= ((100 + critDmg) / 100 * critRate + (100 - critRate)) / 100;
          }
        }
      } else {
        result = 0;
      }
      return result;
    };

    const danCount = computed(() => {
      return itemList('').filter(item => item[0].endsWith('') && !item[0].startsWith('_')).length;
    });

    const displayDamageValue = (item: any, index: number, category: string) => {
      let value = item[index];
      if (!value) return "-";
      if (![""].includes(item[5])) {
        if (.value == "" && ["", ""].includes(item[1])) {
          value *= .;
          if (. && ["", ""].includes(.) && item[1] != .) {
            if (item[1] == '') value *= 1.5;
            else if (item[1] == '') value *= 2;
            if (. == '') value /= 1.5;
            else if (. == '') value /= 2;
          }
        } else if (.value == "" && ["", ""].includes(item[1])) {
          value *= .;
          if (. && ["", ""].includes(.) && item[1] != .) {
            if (item[1] == '') value *= 2;
            else if (item[1] == '') value *= 1.5;
            if (. == '') value /= 2;
            else if (. == '') value /= 1.5;
          }
        } else if (.value == "" && [""].includes(item[1])) {
          let addValue = .;
          // if (item[6]) addValue *= item[6]; // HIT +*CD
          if (item[7]) addValue *= item[7]; // 
          if (item[8]) addValue *= item[8]; // 
          addValue *= item[index] / item[4]; // 2:/3:/4:
          if (item[0] == '' && category == '') {
            addValue *= danCount.value;
          }
          value += addValue;
        } else if (.value == "" && [""].includes(item[1])) {
          let addValue = .;
          // if (item[6]) addValue *= item[6]; // HIT +*CD
          if (item[7]) addValue *= item[7]; // 
          if (item[8]) addValue *= item[8]; // 
          addValue *= item[index] / item[4]; // 2:/3:/4:
          if (item[0] == '' && category == '') {
            addValue *= danCount.value;
          }
          value += addValue;
        }
      }
      if (value < 10) value = Math.round(value * 100) / 100;
      else if (value < 100) value = Math.round(value * 10) / 10;
      else value = Math.round(value);
      return value;
    };

    const CATEGORY_LIST = [
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const itemList = (category: string) => {
      const result = [] as any[];
      const workList = props.damageResult[category].filter(
        (s: any[]) => !s[0].startsWith("")
      );
      for (let i = 0; i < workList.length; i++) {
        let span = 1;
        if (i > 0 && workList[i][0] == workList[i - 1][0]) span = 0;
        else {
          span = workList.slice(i).filter((s: any) => s[0] == workList[i][0]).length;
        }
        result.push([...workList[i], span]);
      }
      return result;
    };

    const categoryOpenClose = reactive({} as { [key: string]: boolean });
    for (const key of CATEGORY_LIST) {
      categoryOpenClose[key] = true;
      // if ([""].includes(key)) categoryOpenClose[key] = false;
    }

    const categoryOnClick = (category: string) => {
      categoryOpenClose[category] = !categoryOpenClose[category];
    };

    const displayNameV = (key: any): string => {
      if (key && i18n.global.locale.value == "ja-jp" && key.length > 10) {
        let work = key;
        work = work.replace(/$/, "").replace(/$/, "");
        return work;
      }
      return displayName(key);
    };

    const displayNameH = (key: any, category: string): string => {
      if (key && i18n.global.locale.value == "ja-jp" && itemList(category).length > 2) {
        let work = key;
        work = work.replace(/$/, "").replace(/$/, "");
        return work;
      }
      return displayName(key);
    };

    const displayDamageParam = (item: any): string => {
      let result = '';
      if (!item || !item[5] || ['HP', '', ''].includes(item[5])) return "";
      result += displayName('');
      result += ':&nbsp;';
      result += Math.round((item[7] - 1) * 1000) / 10;
      result += '%<br>';
      if (item[2] && item[3] && item[4]) {
        let critRate = item[2] - item[4]; //  - 
        critRate /= item[3] - item[4]; //  - 
        critRate = Math.round(critRate * 1000) / 10;
        const critDmg = Math.round((item[3] / item[4] - 1) * 1000) / 10;
        result += displayName('');
        result += ':&nbsp;';
        result += critRate;
        result += '%/';
        result += critDmg;
        result += '%<br>';
      }
      if (item[6]) {
        result += item[6];
        result += 'Hit' + (item[6] > 1 ? 's' : '');
        result += '<br>';
      }
      return result;
    };

    /**  */
    const damageTakenList = computed(() => {
      const result = props.damageResult..filter(
        (s: any) => s.key == ""
      ) as any;
      for (const entry of props.damageResult..filter(
        (s: any) => s.key != ""
      )) {
        const valueArr = result.map((s: any) => s.value);
        if (!valueArr.includes(entry.value)) {
          result.push(entry);
        }
      }
      return result;
    });

    /**  */
    const resScoreList = computed(() => {
      const result = props.damageResult..filter(
        (s: any) => s.key == ""
      ) as any;
      for (const entry of props.damageResult..filter(
        (s: any) => s.key != ""
      )) {
        const valueArr = result.map((s: any) => s.value);
        if (!valueArr.includes(entry.value)) {
          result.push(entry);
        }
      }
      return result;
    });

    return {
      displayName,
      displayNameV,
      displayNameH,

      ,
      ,
      swirlDmg,
      reactionDmg,
      elementClass,
      displayDamageValue,
      displayDamageParam,
      CATEGORY_LIST,
      itemList,
      categoryOpenClose,
      categoryOnClick,
      resultStyleRef,

      damageTakenList,
      resScoreList,
    };
  },
});
